import React, { Component } from "react";
import {
  Form,
  Input,
  Select,
  Button,
  Table,
  Space,
  Card,
  message,
  Modal,
} from "antd";
import { ExclamationCircleOutlined } from "@ant-design/icons";
import "../../css/Juan/UserManagement.css";
import ExportJsonExcel from "js-export-excel";
import Axios from "../../util/Axios";

const { Option } = Select;
const { confirm } = Modal;
export default class UserManagement extends Component {
  constructor(props) {
    super(props);
    this.state = {
      selectedRowKeys: [], // index
      selectedRows: [], //
      userData: [],
      columns: [
        {
          title: "",
          dataIndex: "userName",
        },
        {
          title: "",
          dataIndex: "userPhoneNumber",
        },
        {
          title: "",
          dataIndex: "lastActiveTime",
        },
        {
          title: "",
          dataIndex: "deliveryOrderNumber",
        },
        {
          title: "",
          dataIndex: "siteName",
        },
        {
          title: "",
          dataIndex: "freezeStatus",
        },
        {
          title: "",
          key: "action",
          render: (record) => (
            <Space size="middle">
              <span  onClick={() => this.getDetail(record)}>
                
              </span>
              {/* <a href="#" onClick={() => this.froze(text, record)}>
                
              </a> */}
            </Space>
          ),
        },
      ],
    };
  }
  componentDidMount() {
    this.renderAll();
  }

  //
  renderAll = () => {
    Axios.get("/User/findAll")
      .then((data) => {
        //
        let { data: res } = data;
        console.log(res);
        console.log(res.data);
        if (res.code === "0001") {
          message.error("");
        } else if (res.code === "0000") {
          //
          let revisedData = this.reformData(res.data);
          console.log("====================================");
          console.log(revisedData);
          console.log("====================================");
          //2.State
          this.setState({
            userData: revisedData,
          });
        }
      })
      .catch((erro) => {
        console.log(erro);
      });
  };

  //
  reformData = (data) => {
    let revisedData = data.map((item) => {
      let obj = {};
      obj.freezeStatus = item.freezeStatus;
      obj.deliveryOrderNumber = item.deliveryOrderNumber;
      obj.lastActiveTime = item.lastActiveTime;
      obj.siteName = item.siteName;
      obj.userId = item.userId;
      obj.userName = item.userName;
      obj.userPhoneNumber = item.userPhoneNumber;
      obj.key = item.userId;
      console.log("====================================");
      console.log(obj);
      console.log("====================================");
      return obj;
    });
    console.log(revisedData);
    return revisedData;
  };

  //
  onSelectChange = (selectedRowKeys, selectedRows) => {
    console.log("selectedRowKeys changed: ", selectedRowKeys);
    console.log("selectedRow changed: ", selectedRows);
    this.setState({ selectedRowKeys, selectedRows });
  };

  //
  froze = () => {
    let { selectedRows } = this.state;
    // console.log(selectedRows);
    //id
    let frozeData = selectedRows.map((item) => {
      return item.userId;
    });
    console.log(frozeData);

    //
    confirm({
      title: "?",
      icon: <ExclamationCircleOutlined />,
      content: "",
      okText: "",
      cancelText: "",
      onOk: () => {
        console.log("");
        Axios.post("/User/freezeUser", { id: frozeData, freezeStatus: "" })
          .then((data) => {
            console.log(data);
            //table
            this.renderAll();
          })
          .catch((erro) => {
            console.log(erro);
          });
      },
      onCancel: () => {
        console.log("");
      },
    });
  };

  //
  thawUser = () => {
    let { selectedRows } = this.state;
    // console.log(selectedRows);
    //id
    let thawData = selectedRows.map((item) => {
      return item.userId;
    });
    console.log(thawData);

    //
    confirm({
      title: "?",
      icon: <ExclamationCircleOutlined />,
      content: "",
      okText: "",
      cancelText: "",
      onOk: () => {
        console.log("");
        Axios.post("/User/thawUser", { id: thawData, freezeStatus: "" })
          .then((data) => {
            console.log(data);
            //table
            this.renderAll();
          })
          .catch((erro) => {
            console.log(erro);
          });
      },
      onCancel: () => {
        console.log("");
      },
    });
  };

  //excel
  exportExcel = () => {
    const datas = this.state.userData ? this.state.userData : ""; //
    var option = {};
    let dataTable = []; //
    console.log(datas);
    if (datas) {
      for (const data of datas) {
        console.log(data);
        if (data) {
          let obj = {
            key: data.userId,
            userName: data.userName,
            userPhoneNumber: data.userPhoneNumber,
            lastActiveTime: data.lastActiveTime,
            deliveryOrderNumber: data.deliveryOrderNumber,
            siteName: data.siteName,
            freezeStatus: data.freezeStatus,
          };
          dataTable.push(obj);
        }
      }
    }
    console.log(dataTable);
    option.fileName = ""; //
    option.datas = [
      {
        sheetData: dataTable, //
        sheetName: "",
        sheetHeader: [
          "",
          "",
          "",
          "",
          "",
          "",
          "",
        ], // 
      },
    ];
    var toExcel = new ExportJsonExcel(option);
    toExcel.saveExcel(); //
  };

  //
  onFinish = (values) => {
    // console.log("Success:", values);
    //values
    values.freezeStatus === undefined
      ? (values.freezeStatus = "")
      : (values.freezeStatus = values.freezeStatus);
    values.site === undefined
      ? (values.site = "")
      : (values.site = values.site);
    console.log(values);
    //axios
    Axios.post("/User/findByLike", {
      SiteName: values.site,
      freezeStatus: values.freezeStatus,
      keyWords: values.keywords,
    })
      .then((data) => {
        //
        let { data: res } = data;
        // console.log("=====================");
        // console.log(res);
        // console.log(res.data);
        //
        if (res.code === "0000") {
          //
          let revisedData = this.reformData(res.data);
          //State
          this.setState({
            userData: revisedData,
          });
        }
        //
        if (res.code === "0001") {
          message.error("");
        }
      })
      .catch((erro) => {
        console.log(erro);
      });
  };

  //
  getDetail = (record) => {
    console.log(record);
    this.props.history.push({
      pathname: "/index/juanJ",
      query: { record },
    });
  };

  render() {
    const { selectedRowKeys } = this.state;
    const rowSelection = {
      selectedRowKeys,
      onChange: this.onSelectChange,
    };
    return (
      <div>
        <Form id="userManagement-search" name="search" onFinish={this.onFinish}>
          <Form.Item label="" htmlFor="keywords" name="keywords">
            <Input id="keywords" placeholder="" />
          </Form.Item>

          <Form.Item
            label=""
            htmlFor="userManagement-freezeStatus"
            name="freezeStatus"
          >
            <Select defaultValue="" id="userManagement-freezeStatus">
              <Option value=""></Option>
              <Option value=""></Option>
            </Select>
          </Form.Item>

          <Form.Item label="" htmlFor="site" name="site">
            <Select id="site" defaultValue="">
              <Option value=""></Option>
              <Option value=""></Option>
              <Option value=""></Option>
            </Select>
          </Form.Item>
          <Form.Item>
            <Button htmlType="submit" name="submit">
              
            </Button>
          </Form.Item>
        </Form>
        <Card>
          <Button onClick={this.exportExcel}>Excel</Button>
          <Button id="userManagement-froze" onClick={() => this.froze()}>
            
          </Button>
          <Button id="userManagement-froze" onClick={() => this.thawUser()}>
            
          </Button>
        </Card>

        <Table
          rowSelection={rowSelection}
          columns={this.state.columns}
          dataSource={this.state.userData}
          size="small"
        />
      </div>
    );
  }
}
