import React, { Component } from "react";
import {
  Form,
  Input,
  Select,
  Button,
  Table,
  Space,
  Card,
  DatePicker,
  message,
  Modal,
} from "antd";
import ExportJsonExcel from "js-export-excel";
import "../../css/ZhanDianHouTai/DeliveryList.css";
import Axios from "../../util/Axios";
import FormItem from "antd/lib/form/FormItem";
import moment from "moment";
import "moment/locale/zh-cn";
import locale from "antd/es/date-picker/locale/zh_CN";
import { ExclamationCircleOutlined } from "@ant-design/icons";
// import {withRouter}from "react-router-dom";
const { Option } = Select;
const { RangePicker } = DatePicker;
const { confirm } = Modal;
export default class DeliveryList extends Component {
  constructor(props) {
    super(props);
    this.state = {
      selectedRowKeys: [], // index
      selectedRows: [], //
      orderData: [],
      columns: [
        {
          title: "",
          dataIndex: "id",
        },
        {
          title: "",
          dataIndex: "logisticsId",
          className: "deliveryList-logisticsId",
        },
        {
          title: "",
          dataIndex: "oneName",
        },
        {
          title: "",
          dataIndex: "count",
        },
        {
          title: "",
          dataIndex: "supCount",
        },
        {
          title: "()",
          dataIndex: "price",
        },
        {
          title: "(KG)",
          dataIndex: "heavy",
        },
        {
          title: "",
          dataIndex: "checkStatus",
        },
        {
          title: "",
          dataIndex: "auditSituation",
        },
        {
          title: "",
          dataIndex: "consigneeName",
        },
        {
          title: "",
          dataIndex: "consigneePhoneNumber",
        },
        {
          title: "",
          dataIndex: "staffName",
        },
        {
          title: "",
          dataIndex: "deliveryTime",
        },
        {
          title: "",
          dataIndex: "agingCode",
        },
        {
          title: "",
          dataIndex: "editTime",
        },
        {
          title: "",
          key: "action",
          render: (text, record) => (
            <Space size="middle">
              <Button size='small' onClick={() => this.getDetail(text, record)}>
                
              </Button>
              <Button size='small' onClick={() => this.editDetail(text, record)}>
                
              </Button>
            </Space>
          ),
        },
      ],
      orderStatusList: [
        "",
        "",
        "",
        "",
        "",
        "",
      ],
      successList: [
        "",
        "",
        "",
        "",
        "",
      ],
      failList: [
        "",
        "",
        "",
        "",
      ],
      // secondList: [],
      optionDom: "",
    };
  }
  componentDidMount() {
    this.renderAll();
  }

  //
  renderAll = () => {
    //1.Axios
    Axios.post("/Order/findOrderList")
      .then((data) => {
        //
        let { data: res } = data;
        console.log("====================================");
        console.log(res.data);
        console.log("====================================");
        // 
        let revisedData = res.data.map((item, index) => {
          let obj = {};
          obj.key = index;
          obj.id = item.id;
          obj.logisticsId = item.logisticsId;
          obj.oneName = item.oneName;
          obj.count = item.count;
          obj.supCount = item.supCount;
          obj.price = item.price;
          obj.heavy = item.heavy;
          obj.checkStatus = item.checkStatus;
          obj.auditSituation = item.auditSituation;
          obj.consigneeName = item.consigneeName;
          obj.consigneePhoneNumber = item.consigneePhoneNumber;
          obj.staffName = item.staffName;
          obj.editTime = item.editTime;
          obj.deliveryTime = item.deliveryTime;
          obj.agingCode = item.agingCode;
          // console.log("====================================");
          // console.log(obj);
          // console.log("====================================");
          return obj;
        });
        console.log(revisedData);
        //State
        this.setState({
          orderData: revisedData,
        });
        console.log('end of render')
      })
      .catch((erro) => {
        console.log(erro);
      });
  };

  //
  handleStatusChange = (value) => {
    console.log(value);
    if (value === "") {
      this.setState({
        optionDom: this.state.successList.map((item) => (
          <Option key={item}>{item}</Option>
        )),
      });
    } else if (value === "") {
      this.setState({
        optionDom: this.state.failList.map((item) => (
          <Option key={item}>{item}</Option>
        )),
      });
    } else {
      this.setState({ optionDom: null });
    }
  };

  //
  onSelectChange = (selectedRowKeys, selectedRows) => {
    console.log("selectedRowKeys changed: ", selectedRowKeys);
    console.log("selectedRow changed: ", selectedRows);
    this.setState({ selectedRowKeys, selectedRows });
  };

  //
  batchDel = () => {
    let { selectedRows } = this.state;
    console.log(selectedRows); //
    //
    let req = selectedRows.map((item) => {
      let obj = {};
      obj.id = item.id;
      obj.lgId = item.logisticsId;
      return obj;
    });

    //
    confirm({
      title: "?",
      icon: <ExclamationCircleOutlined />,
      content: "",
      okText: "",
      cancelText: "",
      onOk: () => {
        console.log("");
        //Axios
        Axios.post("/Order/delteOrderbyLg", { lgId: req })
          .then((data) => {
            console.log(data)
            if(data.data.code==='0000'){
              console.log('cancel success')
             this.renderAll()
            }

          })
          .catch((err) => {
            console.log(err);
          });
      },
      onCancel: () => {
        console.log("");
      },
    });
  };

  //
  onFinish = (values) => {
    console.log("Success:", values);

    if (values.state === undefined) {
      values.effect = "";
    }
    if (values.effect === undefined) {
      values.effect = "";
    }
    //axios

    Axios.post("/Order/findSomeOrderList", {
      keyWords: values.keywords,
      deliveryTimestart: values.deliveryTime
        ? moment(values.deliveryTime[0]._d).format("YYYY-MM-DD")
        : "",
      deliveryTimeend: values.deliveryTime
        ? moment(values.deliveryTime[1]._d).format("YYYY-MM-DD")
        : "",
      checkStatus: values.state,
      singCode: values.state1,
      agingCode: values.effect,
    })
      .then((data) => {
        //
        let { data: res } = data;
        console.log("=====================");
        console.log(res);

        console.log(res.data);
        // //
        if (res.code === "0001") {
          //
          let revisedData = res.data.map((item, index) => {
            let obj = {};
            obj.key = index;
            obj.id = item.id;
            obj.logisticsId = item.logisticsId;
            obj.oneName = item.oneName;
            obj.count = item.count;
            obj.supCount = item.supCount;
            obj.price = item.price;
            obj.heavy = item.heavy;
            obj.checkStatus = item.checkStatus;
            obj.auditSituation = item.auditSituation;
            obj.consigneeName = item.consigneeName;
            obj.consigneePhoneNumber = item.consigneePhoneNumber;
            obj.staffName = item.staffName;
            obj.editTime = item.editTime;
            obj.deliveryTime = item.deliveryTime;
            obj.agingCode = item.agingCode;
            // console.log("====================================");
            // console.log(obj);
            // console.log("====================================");
            return obj;
          });
          //State
          this.setState({
            orderData: revisedData,
          });
        }
        // //
        if (res.code === "0000") {
          message.error("");
        }
      })
      .catch((erro) => {
        console.log(erro);
      });
  };

  //
  print = () => {
    let { selectedRows } = this.state;
    console.log(selectedRows); //
  };

  //
  editDetail = (text, record) => {
    console.log(text);
    console.log(record);
    this.props.history.push({
      pathname: "/index/DistributionEditor",
      state: {
        key:record.logisticsId,
        orderId:record.id
      },
    });
  };

  //
  getDetail = (text, record) => {
    console.log(text);
    console.log(record);
    this.props.history.push({
      pathname: "/index/Zjj",
      state: { signNumber:record.logisticsId },
    });
  };

  //excel
  exportExcel = () => {
    const datas = this.state.orderData ? this.state.orderData : ""; //
    var option = {};
    let dataTable = []; //
    console.log(datas);
    if (datas) {
      for (const data of datas) {
        console.log(data);
        if (data) {
          let obj = {
            key: data.key,
            id: data.id,
            oneName: data.oneName,
            count: data.count,
            supCount: data.supCount,
            price: data.price,
            heavy: data.heavy,
            checkStatus: data.checkStatus,
            auditSituation: data.auditSituation,
            consigneeName: data.consigneeName,
            consigneePhoneNumber: data.consigneePhoneNumber,
            staffName: data.staffName,
            deliveryTime: data.deliveryTime,
            agingCode: data.agingCode,
            editTime: data.editTime,
          };
          dataTable.push(obj);
        }
      }
    }
    console.log(dataTable);
    option.fileName = ""; //
    option.datas = [
      {
        sheetData: dataTable, //
        sheetName: "", //sheet // sheetFilter: [dataTable.id, dataTable.name],// // sheetFilter ( data  object )()
        sheetHeader: [
          "",
          "",
          "",
          "",
          "",
          "()",
          "(KG)",
          "",
          "",
          "",
          "",
          "",
          "",
          "",
          "",
        ], //// 
      },
    ];
    var toExcel = new ExportJsonExcel(option);
    toExcel.saveExcel(); //
  };

  render() {
    const { selectedRowKeys } = this.state;
    const rowSelection = {
      selectedRowKeys,
      onChange: this.onSelectChange,
    };
    return (
      <div>
        <Card>
          <Form
            id="deliveryList-search"
            name="search"
            onFinish={this.onFinish}
            onFinishFailed={this.onFinishFailed}
          >
            <Form.Item
              label=""
              htmlFor="keywords"
              name="keywords"
              id="deliverList-keywords"
            >
              <Input id="keywords" placeholder="" />
            </Form.Item>
            <Form.Item
              label=""
              htmlFor="deliveryTime"
              name="deliveryTime"
            >
              <RangePicker
                locale={locale}
                id="deliveryTime"
                format={"YYYY/MM/DD"}
              />
            </Form.Item>
            <Form.Item
              id="deliverList-status"
              label=""
              htmlFor="deliverList-state"
              name="state"
            >
              <Select
                defaultValue=""
                id="deliverList-state"
                onChange={this.handleStatusChange}
              >
                {this.state.orderStatusList.map((item) => (
                  <Option key={item}>{item}</Option>
                ))}
              </Select>
            </Form.Item>

            <FormItem
              label=""
              htmlFor="deliverList-secondList"
              name="state1"
            >
              <Select id="deliverList-secondList" defaultValue="----">
                {this.state.optionDom}
              </Select>
            </FormItem>

            <Form.Item label="" htmlFor="effect" name="effect">
              <Select defaultValue="" id="effect">
                <Option value=""></Option>
                <Option value=""></Option>
              </Select>
            </Form.Item>

            <Form.Item>
              <Button htmlType="submit" name="submit">
                
              </Button>
            </Form.Item>
          </Form>
        </Card>
        <Card>
          <Button onClick={this.exportExcel}>Excel</Button>
          <Button id="deliveryList-batchDel" onClick={() => this.batchDel()}>
            
          </Button>
          {/* <Button id="deliveryList-print" onClick={() => this.print()}>
            
          </Button> */}
        </Card>
        <Table
          rowSelection={rowSelection}
          columns={this.state.columns}
          dataSource={this.state.orderData}
          size="small"
        />
      </div>
    );
  }
}
