import React, { Component } from 'react'
// import { AudioOutlined } from '@ant-design/icons';
import { Radio } from 'antd';
import { Table } from 'antd';
import { Space } from 'antd';
import { DatePicker, Form, Input, Select, Modal, Button } from 'antd';
// const { Column, ColumnGroup } = Table;
import { ExclamationCircleOutlined } from '@ant-design/icons';
import 'antd/dist/antd.css';
import '../../css/Zhao/DistributionTop.css';
import Axios from "../../util/Axios.js";

import ExportJsonExcel from "js-export-excel";

import 'moment/locale/zh-cn'; 
import locale from 'antd/es/date-picker/locale/zh_CN';



const { Option } = Select;
// const { Search } = Input;
const { confirm } = Modal;

const layout = {
    labelCol: { span: 8 },
    wrapperCol: { span: 16 },
};
const tailLayout = {
    wrapperCol: { offset: 8, span: 16 },
};


//=================================input========================================
// const suffix = (
//     <AudioOutlined
//         style={{
//             fontSize: 16,
//             color: '#1890ff',
//         }}
//     />
// );

class DistributionTop extends Component {
    constructor() {
        super();
        this.input_orderId = React.createRef();
        this.input_phone = React.createRef();
        this.input_age = React.createRef();
        this.input_sex = React.createRef();
        this.input_sta = React.createRef();
        this.input_car = React.createRef();
        this.input_mobile = React.createRef();
        this.input_origin = React.createRef();
        this.DeliveryOrigin = React.createRef();
        this.time = React.createRef();
        this.Deliverytime = React.createRef();
        this.state = {
            startTime: '',
            endTime: '',
            // statusCode:'',
            selectedRows: [],
            selectedRowKeys: [], // Check here to configure the default column
            userData: [],
            visible: false,
            data: [],
            // orderId: '',
            phone: '',
            age: '',
            sex: '',
            sta: '',
            car: '',
            mobile: '',
            origin: '',
            DeliveryOrigin: '',
            time: '',
            Deliverytime: '',
            destroyOnClose: true,
            input_key: '',
            add: 'add',
            show:'none',
            block:'none'

        }
    }


    //==========================================================
    DelConfirm() {

        // this.setState({
        //     input_key:mykey
        // });
        confirm({
            title: '?',
            okText:'',
            cancelText:"",
            icon: <ExclamationCircleOutlined  />,
            // content: 'Some descriptions',
            
            onOk: () => {
                const arr = [];

                const { selectedRows } = this.state
                console.log(selectedRows)
                selectedRows.forEach(item => {

                    const id = item.orderId
                    arr.push({ orderId: id,statusCode: 1 })

                    // console.log(arr)

                    Axios.put("/auditManagement",
                        arr
                    ).then((response) => {
                        console.log(response);
                        this.getDistribution();
                    })
                        .catch((error) => {
                            console.log(error);
                        });

                })

            },
            onCancel() {
                console.log('Cancel');
            },
        });
    }
    //====================================================================



    //====================================================
    showConfirm =(checkStatus) => {
        console.log(checkStatus);
        this.props.history.push({
            pathname:"/index/dai",
            query:checkStatus,
        })

    };
    ddConfirm =(record) =>{
        console.log(record);
        this.props.history.push({
            pathname:"/index/scuccess",
            query: record,
        })
    };
    //==========================================================

    //=================================================================
    
    
    onFinish = (values) => {
        console.log(this.state.endTime);
        values.startTime = this.state.startTime;
        values.endTime = this.state.endTime;


        console.log(values);
       

        Axios.post("/auditManagement", values).then(data => {
            console.log(data);
            const mydata = [];
            console.log(data.data.data);
            data.data.data.forEach((vac, index) => {
                mydata.push({
                    orderId: vac.orderId,
                    phone: vac.userName,
                    age: vac.consigneePhoneNumber,
                    sex: vac.consigneeName,
                    sta: vac.startTime,
                    car: vac.street,
                    mobile: vac.consigneeAddress,
                    origin: vac.checkStatus,
                    DeliveryOrigin: vac.orderStatus,
                    time: vac.bornTime,
                    Deliverytime: vac.waitTime,
                    key: index,
                    look:''
                })

            })
            console.log(mydata)

            this.setState({
                data: mydata,
            });
            // this.onFinish()
        });
        console.log(values); // { value: "lucy", key: "lucy", label: "Lucy (101)" }
    }



    // ====================================================
    onSelectChange = (selectedRowKeys, selectedRows) => {

        console.log({selectedRows});
        console.log({selectedRowKeys,selectedRows});
        this.setState({ selectedRowKeys, selectedRows });
    };

    //============================================================================
    componentDidMount() {
        this.getDistribution()

    }
    getDistribution() {
        Axios.get("/auditManagement").then(data => {
            // console.log(data)
            const mydata = [];
            console.log(data.data.data);
            data.data.data.forEach((vac, index) => {
                mydata.push({
                    orderId: vac.orderId,
                    phone: vac.userName,
                    age: vac.consigneePhoneNumber,
                    sex: vac.consigneeName,
                    sta: vac.startTime,
                    car: vac.street,
                    mobile: vac.consigneeAddress,
                    origin: vac.checkStatus,
                    DeliveryOrigin: vac.orderStatus,
                    time: vac.bornTime,
                    Deliverytime: vac.waitTime,
                    key: index,
                    look: ''
                })
            });
            console.log(mydata);
            
            this.setState({
                data: mydata,
            });
          
        })
    }

    

    // ======================================================================
    // UNSAFE_componentWillMount() {
    //     const myData=[];
    //     for (let i = 0; i < 46; i++) {
    //         myData.push({
    //             key: i,
    //             name: `${i+1}`,
    //             phone: `${i}${i}${i}${i}${i}${i}${i}${i}${i}${i}${i}${i}${i}`,
    //             age: `${i+1}`,
    //             sex: `. ${i+1}`,
    //             sta:``,
    //             car:` `,
    //             mobile:` `,
    //             origin:` `,
    //             DeliveryOrigin:`${i+1}-${i+1}${i+1}-${i+1}`,
    //             time:`${i+1}-${i+1}${i+1}-${i+1}`,
    //             Deliverytime:`${i+1}`,
    //             look:''
    //         });
    //     }
    //     this.setState({
    //         data:myData
    //     })
    // }
    // componentWillUpdate(nextProps, nextState, nextContext) {

    // }


    //============================================================



    //==========================================================
    exportExcel = () => {
        const datas = this.state.data ? this.state.data : ""; //
        var option = {};
        let dataTable = []; //
        console.log(datas);
        if (datas) {
            for (const data of datas) {
                console.log(data);
                if (data) {
                    let obj = {
                        // key: data.key,
                        // userName: data.userName,
                        // userTel: data.userTel,
                        // latestUse: data.latestUse,
                        // deliveryNo: data.deliveryNo,
                        // site: data.site,
                        // state: data.state,
                        orderId: data.orderId,
                        phone: data.phone,
                        sex: data.sex,
                        age: data.age,
                        car: data.car,
                        mobile: data.mobile,
                        origin: data.origin,
                        DeliveryOrigin: data.DeliveryOrigin,
                        time: data.time,
                        Deliverytime: data.Deliverytime,
                        key: data.key,
                    };
                    dataTable.push(obj);
                }
            }
        }
        console.log(dataTable);
        option.fileName = ""; //
        option.datas = [
            {
                sheetData: dataTable, //
                sheetName: "", //sheet // sheetFilter: [dataTable.id, dataTable.name],// // sheetFilter ( data  object )()
                sheetHeader: [
                    "",
                    "",
                    "",
                    "",
                    "",
                    "",
                    "",
                    "",
                    "",
                    "",
                    ""
                ], //// 
            },
        ];
        var toExcel = new ExportJsonExcel(option);
        toExcel.saveExcel(); //
    };
    //===============================================================


    getStartTime = (val, datastring) => {
        console.log(datastring);
        this.setState({
            startTime:datastring
        })
    }
    getEndTime = (val, datastring) => {
        console.log(datastring);
        this.setState({
            endTime:datastring
        })
    }

  

    render() {
        console.log("render");
        // console.log(this.state.data);
        // let myData = this.state.data;
        //======================================
        //         const data = [];

        //==========================================================================
        const columns = [
            {
                title: '',
                dataIndex: 'orderId',
            },
            {
                title: '',
                dataIndex: 'phone',
            },
            {
                title: '',
                dataIndex: 'sex',
            },
            {
                title: '',
                dataIndex: 'age',
            },
            {
                title: '',
                dataIndex: 'car',
            },
            {
                title: '',
                dataIndex: 'mobile',
            },
            {
                title: '',
                dataIndex: 'origin',
            },
            {
                title: '',
                dataIndex: 'DeliveryOrigin',
            },
            {
                title: '',
                dataIndex: 'time',
            },
            {
                title: '',
                dataIndex: 'Deliverytime',
            },
            {
                title: '',
                dataIndex: 'key',
            },
            {
                title: '',
                dataIndex: 'look',
                render: (text, record) => (
                    //key

                    <Space size="middle">
                        <span className='btn'  style={record.origin===''? {display:'block'}:{display:'none'}} onClick={() => this.showConfirm(record)}></span>
                        <span className='btn1'  style={record.origin===''? {display:'block'}:{display:'none'}} onClick={() => this.ddConfirm(record)}></span>
                        <span className='btn2'  style={record.origin===''? {display:'block'}:{display:'none'}} onClick={() => this.ddConfirm(record)}></span>
                    </Space>
                )
            }

        ];


        //==============================================================
        const { selectedRowKeys } = this.state;
        const rowSelection = {
            selectedRowKeys,
            onChange: this.onSelectChange,
            selections: [
                
                {
                    key: 'odd',
                    text: '',
                    onSelect: changableRowKeys => {
                        let newSelectedRowKeys = [];
                        newSelectedRowKeys = changableRowKeys.filter((key, index) => {
                            if (index % 2 !== 0) {
                                return false;
                            }
                            return true;
                        });
                        this.setState({ selectedRowKeys: newSelectedRowKeys });
                    },
                },
                {
                    key: 'even',
                    text: '',
                    onSelect: changableRowKeys => {
                        let newSelectedRowKeys = [];
                        newSelectedRowKeys = changableRowKeys.filter((key, index) => {
                            if (index % 2 !== 0) {
                                return true;
                            }
                            return false;
                        });
                        this.setState({ selectedRowKeys: newSelectedRowKeys });
                    },
                },
            ],
        };
        return (
            <div>
                <div>
                    <Form
                        {...layout}
                        id="DistributionTop-search"
                        name="DistributionTop-search"
                        onFinish={this.onFinish}
                    >
                        <Form.Item label="" htmlFor="keywords" name={`vague`}  >
                            <Input id="keywords" placeholder="" />
                        </Form.Item>

                        <Form.Item label="" htmlFor="state" name={`checkStatus`}  style={{width:200}}>
                            <Select id="state">
                                <Option value=""></Option>
                                <Option value=""></Option>
                                <Option value=""></Option>
                            </Select>
                        </Form.Item>

                        <Form.Item label="" htmlFor="ftime" name={`startTime`} className="DistributionleftSearch"style={{width:200}}>
                            <Space direction="vertical">
                                <DatePicker onChange={this.getStartTime} locale={locale}/>
                            </Space>
                        </Form.Item>

                        <Form.Item label="" htmlFor="ftime" name={`endTime`} className="DistributionTopSearch" style={{width:200}}>
                            <Space direction="vertical" >
                                <DatePicker onChange={this.getEndTime} locale={locale}/>
                            </Space>
                        </Form.Item>


                        <Form.Item  {...tailLayout}>
                            <Button htmlType="submit" onFinish={this.onFinish} > </Button>
                        </Form.Item>
                    </Form>
                    <div className='DistributionTopbutton'>
                        <Radio.Button vacue="default" onClick={() => { this.DelConfirm(this.state.selectedRowKeys) }} className="DistributionTopbutton"></Radio.Button>
                        <Button onClick={this.exportExcel} className="DistributionTopbutton2"></Button>
                    </div>
                 

                </div>

                <div>
                    <Table rowSelection={rowSelection} columns={columns} dataSource={this.state.data} size='small' />
                </div>

                {/*{===============================}*/}
                {/* <div className="myModal">

                </div> */}


            </div>
        );
    }
}

export default DistributionTop