import React, { Component } from 'react'
import { Button, Divider, Table, Typography, message } from "antd";


import '../../../css/ZhanDianHouTai/DispatchDetails.css'
import Axios from '../../../util/Axios';

const { Title } = Typography;

class DispatchDetails extends Component {
  constructor(props) {
    super(props);
    this.state = {
      courierMsg: '',
      orderDetailsMsg: '',//
      orderDetails: '',//
      columns: [
        {
          title: '1',
          dataIndex: 'classification1',
          align: 'center'
        },
        {
          title: '2',
          dataIndex: 'classification2',
          align: 'center'
        },
        {
          title: '',
          dataIndex: 'goodsNum',
          align: 'center'
        }
      ]
    }
  }
  // 
  UNSAFE_componentWillMount() {
    // console.log(this.props.location.state.DetailsMsg);

    // const data = [];
    // for (let i = 0; i < 3; i++) {
    //     data.push({
    //         key: i,
    //         classification1: '',
    //         classification2: '',
    //         goodsNum: 123 + i
    //     });
    // }
    // this.setState({
    //   orderDetailsMsg: this.props.location.state.DetailsMsg,
    //   // orderDetails:data
    // })
  }

  componentDidMount() {
    let myData = [];
    this.props.location.state.DetailsMsg.orderInfo.forEach((item, index) => {
      myData.push({
        key: index,
        classification1: item.parentName,
        classification2: item.sonName,
        goodsNum: item.count
      })
    });
    this.setState({
      orderDetailsMsg: this.props.location.state.DetailsMsg.orderInfo[0],
      orderDetails: myData,
      courierMsg: this.props.location.state.DetailsMsg.courierId
    })
  }
  //
  goBackPage = () => {
    window.history.back(-1)
  };

  // 
  platformDispatch = () => {
    console.log("================")
    let that = this
    let courierId = this.state.courierMsg.staffId;
    let orderId = this.state.orderDetailsMsg.orderId;
    console.log(this.state.courierMsg.staffId);
    console.log(this.state.orderDetailsMsg.orderId);
    Axios.post('/marki/platformDispatch', {
      courierId: courierId,
      orderId: orderId
    }).then(res => {
      console.log(res);
      that.props.history.push({
        pathname: '/index/FindReadList',
      })
      if (res.data.msg === "") {
        message.success('');

      } else {
        message.error('');
      }
    })
  }
  render() {
    console.log(this.state);
    //
    let paginationProps = '';
    if (this.state.orderDetails.length >= 10) {
      paginationProps = true
    } else {
      paginationProps = false

    }
    return (
      <div id='DispatchDetails'>
        <div className='detailsTitle'>
          <Button size="small" onClick={this.goBackPage}></Button>
          <p>{this.state.orderDetailsMsg.signStatus}</p>
        </div>
        <Divider />
        <div id='detailsMsg'>
          <div>
            <p>{this.state.orderDetailsMsg.orderId}</p>
            <p>{this.state.courierMsg.staffName}</p>
          </div>
          <div>
            <p>{this.state.orderDetailsMsg.userName}</p>
            <p>{this.state.courierMsg.staffPhoneNumber}</p>
          </div>
          <div>
            <p>{this.state.orderDetailsMsg.consigneeName}</p>
            <p>{this.state.orderDetailsMsg.heavy}KG</p>
          </div>
          <div>
            <p>{this.state.orderDetailsMsg.consigneePhoneNumber}</p>
            <p>{this.state.orderDetailsMsg.street}</p>
          </div>
          <div>
            <p>{this.state.orderDetailsMsg.consigneeAddress}</p>
          </div>
        </div>
        <Divider />


        <div id='orderDetails'>
          <Title level={4}></Title>
          <div>
            <Table locale={{ emptyText: '' }} size='small' columns={this.state.columns} dataSource={this.state.orderDetails} bordered style={{ width: 300 }}
              pagination={paginationProps} />
            <p>{this.state.orderDetailsMsg.supName}{this.state.orderDetailsMsg.supCount}</p>
            <p>{this.state.orderDetailsMsg.heavy}</p>
            <p>{this.state.orderDetailsMsg.price}</p>
            <p>{this.state.orderDetailsMsg.supName}{this.state.orderDetailsMsg.supCount}</p>
          </div>
          <div id='toPeiSon'>
            <Button onClick={this.platformDispatch}></Button>
          </div>
        </div>
      </div>
    )
  }
}

export default DispatchDetails