import React, { Component } from 'react'
import { Input, Select, Button, Space, Table, Form, Row, Col, Popconfirm, DatePicker } from 'antd';
import ExportJsonExcel from "js-export-excel"; //excel
import 'moment/locale/zh-cn';
import locale from 'antd/es/date-picker/locale/zh_CN';
import '../../css/zj/OrderManagementZ.css'

import Axios from '../../util/Axios'


const { Option } = Select;
const { RangePicker } = DatePicker;
// const {initialValues} = Form


class OrderManagementZ extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isloading: true,
      selectedRowKeys: [],//index
      selectedRows: [], //
      orderData: [],//
      startDistribution: '',
      endDistribution: '',
      columns: [
        { title: '', dataIndex: 'orderNumber', align: 'center' },
        { title: '', dataIndex: 'goodsCategory', align: 'center' },
        { title: '', dataIndex: 'goodsQuantity', align: 'center' },
        { title: '', dataIndex: 'casesNumber', align: 'center' },
        { title: '', dataIndex: 'goodsPrice', align: 'center' },
        { title: '', dataIndex: 'goodsHeight', align: 'center' },
        { title: '', dataIndex: 'orderStatus', align: 'center' },
        { title: '', dataIndex: 'userName', align: 'center' },
        { title: '', dataIndex: 'addressee', align: 'center' },
        { title: '', dataIndex: 'contactInformation', align: 'center' },
        { title: '', dataIndex: 'distributor', align: 'center' },
        { title: '', dataIndex: 'deliveryTime', align: 'center' },
        { title: '', dataIndex: 'product', align: 'center' },
        { title: '', dataIndex: 'site', align: 'center' },
        {
          title: '',
          dataIndex: 'operation',
          align: 'center',
          render: (text, record, index) =>
            <Space className='operationBtns'>
              <Button type="link" size='small' onClick={() => this.getOrderMsg(record)}></Button>
              <Popconfirm okText="" cancelText="" title="?" onConfirm={() => this.handleDelete(record)}>
                <Button type='link'></Button>
              </Popconfirm>

            </Space>
        },
      ]

    }
  }

  componentDidMount() {
    Axios.post('/Order/findOrderList')
      .then(res => {
        console.log(res.data.msg);
        if (res.data.msg === '') {
          
          this.setState({ isloading: false })
        }
        console.log(res.data.data);
        this.getListS(res.data.data)
      })
  }

  getListS = (msg) => {
    let myData = [];
    msg.forEach((val, index) => {
      myData.push({
        key: index,
        logisticsId: val.logisticsId,
        orderNumber: val.id,
        goodsCategory: val.oneName,
        goodsQuantity: val.count,
        casesNumber: val.supCount,
        goodsPrice: val.price,
        goodsHeight: val.heavy,
        orderStatus: val.checkStatus,
        userName:val.userName,
        addressee: val.consigneeName,
        contactInformation: val.consigneePhoneNumber,
        distributor: val.staffName,
        deliveryTime: val.deliveryTime,
        product: val.agingCode,
        site: val.siteName,
        operation: ''
      });

    })
    //2.State
    this.setState({
      orderData: myData,
    });
  }
  //
  onSelectChange = (selectedRowKeys, selectedRows) => {
    console.log('selectedRowKeys changed: ', selectedRowKeys);
    console.log(selectedRows);
    this.setState({ selectedRowKeys, selectedRows });
  };
  //excel
  exportExcel = () => {
    // console.log('========')
    const datas = this.state.orderData ? this.state.orderData : ""; //
    let option = {};
    let dataTable = []; //
    // console.log(datas);
    if (datas) {
      for (const data of datas) {
        // console.log(data);
        if (data) {
          let obj = {
            key: data.key,
            orderNumber: data.orderNumber,
            goodsCategory: data.goodsCategory,
            goodsQuantity: data.goodsQuantity,
            casesNumber: data.casesNumber,
            goodsPrice: data.goodsPrice,
            goodsHeight: data.goodsHeight,
            orderStatus: data.orderStatus,
            addressee: data.addressee,
            contactInformation: data.contactInformation,
            distributor: data.distributor,
            deliveryTime: data.deliveryTime,
            product: data.product,
            site: data.site,
          };
          dataTable.push(obj);
        }
      }
    }
    console.log(dataTable);
    option.fileName = ""; //
    option.datas = [
      {
        sheetData: dataTable, //
        sheetName: "", //sheet // sheetFilter: [dataTable.id, dataTable.name],// // sheetFilter ( data  object )()
        sheetHeader: [
          "",
          "",
          "",
          "",
          "",
          "",
          "",
          "",
          "",
          "",
          "",
          "",
          "",
        ], //// 
      },
    ];
    let toExcel = new ExportJsonExcel(option);
    toExcel.saveExcel(); //
  };

  //
  getOrderMsg = (record) => {
    console.log('=================')
    console.log(record)
    console.log(record.logisticsId)
    this.props.history.push({
      pathname: '/index/Zjj',
      state: { signNumber: record.logisticsId }
    })
  };
  // 
  handleDelete = key => {
    const idList = [];
    console.log(key);
    idList.push({id:key.orderNumber, lgId:key.logisticsId})
    console.log(idList);
    Axios.post('/Order/delteOrderbyLg', {
      lgId: idList
    })
      .then(res => {
        console.log(res);
        Axios.post('/Order/findOrderList')
        .then(res => {
          console.log(res.data.msg);
          if (res.data.msg === '') {
            
            this.setState({ isloading: false })
          }
          console.log(res.data.data);
          this.getListS(res.data.data)
        })
      })
      .catch(err => {
        console.log(err);
      })

  };
  //
  batchDeletion = () => {
    let idList = [];
    //ididList
    const { selectedRows } = this.state;
    selectedRows.forEach(item => {
      idList.push({id:item.orderNumber,lgId:item.logisticsId})
    });
    console.log(idList);

    // /Order/delteOneOrderbyLg
    Axios.post('/Order/delteOrderbyLg', {
      lgId: idList
    })
      .then(res => {
        console.log(res);
        Axios.post('/Order/findOrderList')
        .then(res => {
          console.log(res.data.msg);
          if (res.data.msg === '') {
            
            this.setState({ isloading: false })
          }
          console.log(res.data.data);
          this.getListS(res.data.data)
        })
      })
      .catch(err => {
        console.log(err);
      })
  };
  //
  handleSelectTime = (value, dateString) => {
    console.log('', dateString);
    this.setState({
      startDistribution: dateString[0],
      endDistribution: dateString[1],
    })
  };
  // 
  getFields = () => {
    // const count =  6;
    const children = [];
    const dateFormat = 'YYYY-MM-DD';
    children.push(
      <Col span={8} key='1'>
        <Form.Item
          name={`keyWords`}
          label={`:`}
        // rules={[
        //   {
        //     required: true,
        //     message: '!',
        //   },
        // ]}
        >
          <Input size="small" placeholder="" />
        </Form.Item>
        <p id='searchTitle'></p>

      </Col>,
      <Col span={16} key='2'>
        <Form.Item
          label=''
        >
          <RangePicker size='small' format={dateFormat} locale={locale} onChange={this.handleSelectTime} />
        </Form.Item>
        <p id='searchTitle'></p>

      </Col>,

      <Col span={8} key='4'>
        <Form.Item
          name='checkStatus'
          label=''
        // rules={[
        //   {
        //     required: true,
        //     message: '!',
        //   },
        // ]}
        >
          <Select size="small" placeholder=''>
            <Option value=""></Option>
            <Option value=""></Option>
            <Option value=""></Option>
            <Option value=""></Option>
          </Select>
        </Form.Item>
      </Col>,
      <Col span={8} key='5'>
        <Form.Item
          name='siteName'
          label=''
        // rules={[
        //   {
        //     required: true,
        //     message: '!',
        //   },
        // ]}
        >
          <Select size="small" placeholder=''>
            <Option value=""></Option>
            <Option value=""></Option>
            <Option value=""></Option>

          </Select>
        </Form.Item>
      </Col>
    );
    // console.log(children)
    return children;
  };

  //
  resetQuery = () => {
    this.setState({ isloading: true });
    Axios.post('/Order/findOrderList')
      .then(res => {
        console.log(res.data.msg);
        if (res.data.msg === '') {
          this.setState({ isloading: false })
        }
        console.log(res.data.data);
        this.getListS(res.data.data)
      })
  };

  // =================================================================
  render() {
    //  
    const onFinish = values => {
      this.setState({ isloading: true });
      values.deliveryTimestart = this.state.startDistribution;
      values.deliveryTimeend = this.state.endDistribution;
      // console.log('Received values of form: ', values);
      Axios.post('/Order/findSomeOrderList', values)
        .then(res => {
          console.log(res);
          if (res.data.msg === '') {
            this.setState({ isloading: false })
          }
          this.getListS(res.data.data)
        })
    };

    const { selectedRowKeys } = this.state;
    const rowSelection = {
      selectedRowKeys,
      onChange: this.onSelectChange,
    };

    return (
      <div id='allOrderSearch'>
        <div className='orderSearch'>
          <Form
            name="advanced_search"
            className="ant-advanced-search-form"
            onFinish={onFinish}
          >
            <Row gutter={24}>{this.getFields()}</Row>
            <Row>
              <Col
                span={24}
                style={{
                  textAlign: 'right',
                }}
              >
                <Space size='large'>
                  <Button type="primary" htmlType="submit"></Button>
                  <Button onClick={this.resetQuery}></Button>
                </Space>

              </Col>
            </Row>
          </Form>
        </div>
        <div className='orderList'>
          <div className='btns'>
            <Popconfirm okText="" cancelText="" title="?" onConfirm={this.batchDeletion}>
              <Button size="small" ></Button>
            </Popconfirm>
            <Button size="small" onClick={this.exportExcel}></Button>
          </div>
          <div>
            <Table locale={{emptyText:''}} loading={this.state.isloading} size='small' rowSelection={rowSelection} columns={this.state.columns} dataSource={this.state.orderData} />
          </div>
        </div>
      </div>
    )
  }
}

export default OrderManagementZ