class Distribution extends Component {
    constructor() {
        super();
        this.input_name = React.createRef();
        this.input_phone = React.createRef();
        this.input_age = React.createRef();
        this.input_sex = React.createRef();
        this.input_sta = React.createRef();
        this.input_car = React.createRef();
        this.input_mobile = React.createRef();
        this.state = {
            freezelist: [],
            selectedRows: [],
            selectedRowKeys: [], // Check here to configure the default column
            userData: [],
            visible: false,
            data: [],
            name: '',
            phone: '',
            age: '',
            sex: '',
            sta: '',
            car: '',
            mobile: '',
            destroyOnClose: true,
            input_key: '',
            add: 'add',
            // disabled:true,
        }
    }

    //============================
    showModal = (mykey) => {
        console.log(mykey);
        if (mykey === 'add') {
            // console.log(12345);
            this.setState({
                visible: true,
                name: '',
                phone: '',
                age: '',
                sex: '',
                sta: '',
                car: '',
                mobile: '',
                input_key: mykey,
                destroyOnClose: false,
                // disabled:false
            })
        }
        // console.log(this.state.data[mykey]);
        else {
            this.setState({
                visible: true,
                name: this.state.data[mykey].name,
                phone: this.state.data[mykey].phone,
                age: this.state.data[mykey].age,
                sex: this.state.data[mykey].sex,
                sta: this.state.data[mykey].sta,
                car: this.state.data[mykey].car,
                mobile: this.state.data[mykey].mobile,
                input_key: mykey,
                destroyOnClose: false
            });
        }

    };

    handleOk = e => {
        if (this.state.input_key === 'add') {

            console.log('');
            console.log(this.state.data.length);

            let inputK = this.state.data.length;
            let inputL = this.input_name.current.state.value;
            let inputN = this.input_phone.current.state.value;
            let inputC = this.input_age.current.state.value;
            let inputR = this.input_sex.current.state.value;
            let inputg = this.input_sta.current.state.value;
            let inputP = this.input_car.current.state.value;
            let inputf = this.input_mobile.current.state.value;
            let items = [...this.state.data];
            items[this.state.input_key] = {
                key: inputK,
                name: inputL,
                phone: inputN,
                age: inputC,
                sex: inputR,
                sta: inputg,
                operation: '',
                car: inputP,
                mobile: inputf
            };


            let myitem = {

                staffName: inputL,
                staffPhoneNumber: inputN,
                age: inputC,
                sex: inputR,
                freeze: inputg,
                van: inputP,
                batteryCar: inputf,
            };

            this.setState({
                visible: false,
                data: items
            },

                () => {
                    Axios.post("/marki", myitem).then((response) => {
                        console.log(response);
                        this.getDistribution();
                        info()
                    })
                        .catch((error) => {
                            console.log(error);
                        });

                },
            );

            const info = () => {
                message.info('');
            };
            console.log('')


        } else {
            console.log('');
            // console.log(this.input_name.current.state.value);
            let inputK = this.state.input_key;
            let inputL = this.input_name.current.state.value;
            let inputN = this.input_phone.current.state.value;
            let inputC = this.input_age.current.state.value;
            let inputR = this.input_sex.current.state.value;
            let inputg = this.input_sta.current.state.value;
            let inputP = this.input_car.current.state.value;
            let inputf = this.input_mobile.current.state.value;
            let items = [...this.state.data];

            items[this.state.input_key] = {
                key: inputK,
                name: inputL,
                phone: inputN,
                age: inputC,
                sex: inputR,
                sta: inputg,
                operation: '',
                car: inputP,
                mobile: inputf
            };

            let myitem = {

                staffName: inputL,
                staffPhoneNumber: inputN,
                age: inputC,
                sex: inputR,
                freeze: inputg,
                van: inputP,
                batteryCar: inputf,
                staffId: this.state.data[this.state.input_key].staffId,
            };

            // console.log(items);
            console.log(this.state.data[this.state.input_key]);

            this.setState({
                visible: false,
                data: items,
                destroyOnClose: true,
            },

                () => {
                    Axios.put("/marki", myitem).then((response) => {
                        console.log(response);
                        this.getDistribution();
                        info()
                    })
                        .catch((error) => {
                            console.log(error);
                        });
                },

            );
            const info = () => {
                message.info('');
            };
            console.log('')
        }

        // console.log(this.state.data);
        setTimeout(this.clearContent(), 2000)
    };
    //============
    clearContent() {
        this.setState({
            destroyOnClose: true
        })
    }
    handleCancel = e => {
        console.log(e);
        this.setState({
            visible: false,
            destroyOnClose: true
        });
    };
    //==============================================================
    // changePwd=(mykey)=>{
    //     let items=[...this.state.data];
    //     items[mykey].pwd='123456';
    //     console.log(items);

    //     this.setState({
    //         data:items,
    //     })
    // };




    //==========================================================
    DelConfirm() {

        // this.setState({
        //     input_key:mykey
        // });
        confirm({
            title: '?',
            okText:'',
            cancelText:'',
            icon: <ExclamationCircleOutlined />,
            // content: 'Some descriptions',

            onOk: () => {
                const arr = [];

                const { selectedRows } = this.state
                console.log(selectedRows)
                selectedRows.forEach(item => {

                    const id = item.staffId
                    arr.push({ staffId: id })

                    console.log(arr)

                    Axios.post("marki/deleteMarki",
                        arr
                    ).then((response) => {
                        console.log(response);
                        this.getDistribution();
                    })
                        .catch((error) => {
                            console.log(error);
                        });

                })

            },
            onCancel() {
                console.log('Cancel');
            },
        });
    }
    //====================================================================

    //==========================================================
    frozenConfirm() {

        confirm({
            title: '?',
            okText:'',
            cancelText:'',
            icon: <ExclamationCircleOutlined />,

            onOk: () => {
                const arr1 = [];

                const { selectedRows } = this.state
                console.log(selectedRows)
                selectedRows.forEach(item => {

                    const id = item.staffId
                    arr1.push({ staffId: id })
                })
                // this.setState({
                //     freezelist:arr1
                // })          

                console.log(arr1)

                Axios.post("marki/freeze",
                    arr1
                ).then((response) => {
                    console.log(response);
                    this.getDistribution();
                })
                    .catch((error) => {
                        console.log(error);
                    });
            },
            onCancel() {
                console.log('Cancel');
            },
        });

    }
    //====================================================================

    //==========================================================
    thawConfirm() {

        confirm({
            title: '?',
            okText:'',
            cancelText:'',
            icon: <ExclamationCircleOutlined />,

            onOk: () => {
                const arr2 = [];

                const { selectedRows } = this.state
                console.log(selectedRows)
                selectedRows.forEach(item => {

                    const id = item.staffId
                    arr2.push({ staffId: id })
                })
                console.log(arr2)


                Axios.post("marki/notFreeze",
                    arr2
                ).then((response) => {
                    console.log(response);
                    this.getDistribution();
                })
                    .catch((error) => {
                        console.log(error);
                    });


            },
            onCancel() {
                console.log('Cancel');
            },
        });

    }
    //====================================================================

    //=================================================================
    onFinish = (values) => {
        console.log(values);
        console.log(values.search);

        Axios.post("marki/findLike", {
            staffName: values.search,
            staffPhoneNumber: values.search,
            freeze: values.select
        }).then(data => {
            console.log(data)
            const mydata = [];
            console.log(data.data.data);
            data.data.data.forEach((val, index) => {
                mydata.push({
                    name: val.staffName,
                    phone: val.staffPhoneNumber,
                    age: val.age,
                    sex: val.sex,
                    sta: val.freeze,
                    car: val.van,
                    mobile: val.batteryCar,
                    staffId: val.staffId,
                    key: index,
                    look: ''
                })

            })
            console.log(mydata)

            this.setState({
                data: mydata,
            });
            // this.onFinish()
        });
        console.log(values); // { value: "lucy", key: "lucy", label: "Lucy (101)" }
    }

    // onFinish = (values) => {
    //     console.log("success", values);
    // }



    //=======================================================================

    //====================================================
    showConfirm = (record) => {
        let cgoods = <div>
            <div>{record.staffId}</div>
            <div>{record.name}</div>
            <div>{record.phone}</div>
            <div>{record.age}</div>
            <div>{record.sex}</div>
            <div>{record.sta}</div>
        </div>

        confirm({
            title: '',
            okText:'',
            cancelText:'',
            icon: <ExclamationCircleOutlined />,
            content: cgoods,

            onCancel() {
                console.log('Cancel');
            },
        });
    }
    //==========================================================





    //====================================================
    onSelectChange = (selectedRowKeys, selectedRows) => {

        // console.log({selectedRows});
        // console.log({selectedRowKeys,selectedRows});
        this.setState({ selectedRowKeys, selectedRows });
    };


    //============================================================================
    componentDidMount() {
        this.getDistribution()
    }
    getDistribution() {
        Axios.get("/marki").then(data => {
            console.log(data)
            const mydata = [];
            console.log(data.data.data);
            data.data.data.forEach((val, index) => {
                mydata.push({
                    name: val.staffName,
                    phone: val.staffPhoneNumber,
                    age: val.age,
                    sex: val.sex,
                    sta: val.freeze,
                    car: val.van,
                    mobile: val.batteryCar,
                    staffId: val.staffId,
                    key: index,
                    look: ''
                })
            })
            console.log(mydata)

            this.setState({
                data: mydata,
            });

        })
    }

    //=================================================================================


    // UNSAFE_componentWillMount() {
    //     // const myData=[];
    //     // for (let i = 0; i < 46; i++) {
    //     //     myData.push({
    //     //         key: i,
    //     //         name: `${i}`,
    //     //         phone: `${i}${i}${i}${i}${i}${i}${i}${i}${i}${i}${i}${i}${i}`,
    //     //         age: `${i+1}`,
    //     //         sex: `. ${i}`,
    //     //         sta:``,
    //     //         car:` `,
    //     //         mobile:` `,
    //     //         origin:` `,
    //     //         look:''
    //     //     });
    //     // }
    //     // this.setState({
    //     //     data:myData
    //     // })
    // }
    // componentWillUpdate(nextProps, nextState, nextContext) {
    // }


    //==========================================================
    exportExcel = () => {
        const datas = this.state.data ? this.state.data : ""; //
        var option = {};
        let dataTable = []; //
        console.log(datas);
        if (datas) {
            for (const data of datas) {
                console.log(data);
                if (data) {
                    let obj = {
                        // key: data.key,
                        // userName: data.userName,
                        // userTel: data.userTel,
                        // latestUse: data.latestUse,
                        // deliveryNo: data.deliveryNo,
                        // site: data.site,
                        // state: data.state,
                        name: data.name,
                        phone: data.phone,
                        age: data.age,
                        sex: data.sex,
                        sta: data.sta,
                        car: data.car,
                        mobile: data.mobile,
                        origin: data.origin,
                    };
                    dataTable.push(obj);
                }
            }
        }
        console.log(dataTable);
        option.fileName = ""; //
        option.datas = [
            {
                sheetData: dataTable, //
                sheetName: "", //sheet // sheetFilter: [dataTable.id, dataTable.name],// // sheetFilter ( data  object )()
                sheetHeader: [

                    "",
                    "",
                    "",
                    "",
                    "",
                    "",
                    "",

                ], //// 
            },
        ];
        var toExcel = new ExportJsonExcel(option);
        toExcel.saveExcel(); //
    };
    //===============================================================



    render() {
        console.log("render");
        console.log(this.state.data);
        // let myData = this.state.data;
        //======================================
        //         const data = [];

        //==========================================================================
        const columns = [
            {
                title: '',
                dataIndex: 'name',
            },
            {
                title: '',
                dataIndex: 'phone',
            },
            {
                title: '',
                dataIndex: 'sex',
            },
            {
                title: '',
                dataIndex: 'age',
            },
            {
                title: '',
                dataIndex: 'sta',
            },
            {
                title: '',
                dataIndex: 'car',
            },
            {
                title: '',
                dataIndex: 'mobile',
            },
            {
                title: '',
                dataIndex: 'look',
                render: (text, record) => (
                    //key

                    <Space size="middle">
                        <span  onClick={() => this.showModal(record.key)} className='bton'></span>
                        <span onClick={() => this.showConfirm(record)}  className='bton'></span>
                    </Space>
                )
            }

        ];


        //==============================================================
        const { selectedRowKeys } = this.state;
        const rowSelection = {
            selectedRowKeys,
            onChange: this.onSelectChange,
            selections: [
           
                {
                    key: 'odd',
                    text: '',
                    onSelect: changableRowKeys => {
                        let newSelectedRowKeys = [];
                        newSelectedRowKeys = changableRowKeys.filter((key, index) => {
                            if (index % 2 !== 0) {
                                return false;
                            }
                            return true;
                        });
                        this.setState({ selectedRowKeys: newSelectedRowKeys });
                    },
                },
                {
                    key: 'even',
                    text: '',
                    onSelect: changableRowKeys => {
                        let newSelectedRowKeys = [];
                        newSelectedRowKeys = changableRowKeys.filter((key, index) => {
                            if (index % 2 !== 0) {
                                return true;
                            }
                            return false;
                        });
                        this.setState({ selectedRowKeys: newSelectedRowKeys });
                    },
                },
            ],
        };
        return (
            <div>
                <div className="DistributionBox">
                    <Form
                        {...layout}
                        id="Distribution-search"
                        name="Distribution-search"
                        onFinish={this.onFinish}
                        className='Distributionb'
                    >
                        <Form.Item label="" name={`search`} htmlFor="keywords" className="DistributionSearch">
                            <Input id="keywords" placeholder="" />
                        </Form.Item>

                        <Form.Item label="" htmlFor="state" name={`select`} className="DistributionState" >
                            <Select>
                                <Option value=""></Option>
                                <Option value=""></Option>
                            </Select>
                        </Form.Item>

                        <Form.Item className="DistributionStatebutton" {...tailLayout}>
                            <Button htmlType="submit" onFinish={this.onFinish}  ></Button>
                        </Form.Item>
                    </Form>
                    <div className="Distributionbtn">
                        <Radio.Button value="default" onClick={() => { this.DelConfirm(this.state.selectedRowKeys) }}  className='Distributionbtn1'></Radio.Button>
                        <Radio.Button value="default" onClick={() => { this.frozenConfirm(this.state.selectedRowKeys) }} className='Distributionbtn1'></Radio.Button>
                        <Radio.Button value="default" onClick={() => { this.thawConfirm(this.state.selectedRowKeys) }}  className='Distributionbtn1'></Radio.Button>
                        <Radio.Button value="default" onClick={() => { this.showModal(this.state.add) }} className='Distributionbtn1'></Radio.Button>
                        <Button onClick={this.exportExcel} className='Distributionbtn1'></Button>
                    </div>
                    
                </div>

                <div className="Distributiontab">
                    <Table rowSelection={rowSelection} columns={columns} dataSource={this.state.data} />
                </div>

                {/*{===============  ==============================}*/}
                <div className="myModal">
                    <Modal
                        title=""
                        okText=''
                        cancelText=''
                        visible={this.state.visible}
                        onOk={this.handleOk}
                        onCancel={this.handleCancel}
                        destroyOnClose={this.state.destroyOnClose}
                    >
                        <label></label>
                        <Input defaultValue={this.state.name} ref={this.input_name} />
                        <label></label>
                        <Input defaultValue={this.state.phone} ref={this.input_phone} />
                        <label></label>
                        <Input defaultValue={this.state.sex} ref={this.input_sex} />
                        <label></label>
                        <Input defaultValue={this.state.age} ref={this.input_age} type={"number"} min='0' max='100' />
                        <label></label>
                        <Input defaultValue={this.state.sta} ref={this.input_sta} type={"string"} disabled />
                        <label></label>
                        <Input defaultValue={this.state.car} ref={this.input_car} />
                        <label></label>
                        <Input defaultValue={this.state.mobile} ref={this.input_mobile} />
                    </Modal>
                </div>


            </div>
        );
    }
}

export default Distribution
