import React, { Component } from 'react'
import { Col, Form, Row, Select, Typography, Input, Button, Table,message } from "antd";
import Axios from '../../../util/Axios';




// import '../../css/F-DeliveryList/NewLogistics.css'

const { Option } = Select
const { Title } = Typography;
class newLogistics extends Component {
  constructor() {
    super()
    this.columns = [
      {
        title: '1',
        dataIndex: 'classification1',
        editable: true,
        align: 'center'
      },
      {
        title: '2',
        dataIndex: 'classification2',
        editable: true,
        align: 'center'
      },
      {
        title: '3',
        dataIndex: 'classification3',
        editable: true,
        align: 'center'
      },
      {
        title: '',
        dataIndex: 'num',
        editable: true,
        align: 'center'
      },
    ];
    this.state = {
      aa: '',
      gradeOne: [],
      gradeTwo: [],
      gradeThree: [],
      supArr: [],
      allMsg: {}

    };
  }
  componentDidMount() {
    Axios.get('/logistics')
      .then(res => {
        console.log(res);

        if (res.status === 200) {
          let temp1 = [];
          res.data.data.categoryName.forEach(item => {
            temp1.push({ name: item.aname, id: item.categoryOne })
          })
          const id = 'id';
          const arr = temp1.reduce((all, next) => all.some((atom) => atom[id] === next[id]) ? all : [...all, next], []);

          this.setState({
            gradeOne: arr,
            allMsg: res.data.data
          })
        }


      })
  }

  //1
  getOne = () => {
    let data = []
    this.state.gradeOne.forEach(item => {
      data.push(<Option key={item.id} value={item.id}>{item.name}</Option>)
    })
    return data
  }
  //2
  getTwo = () => {
    let data = []
    this.state.gradeTwo.forEach(item => {
      data.push(<Option key={item.id} value={item.id}>{item.name}</Option>)
    })
    return data
  }
  getThree = () => {
    let data = []
    this.state.gradeThree.forEach(item => {
      data.push(<Option key={item.id} value={item.id}>{item.name}</Option>)
    })
    return data
  }

  //
  goBackPage = () => {
    window.history.back(-1)
  };
  //  
  oneChange = (value) => {
    // console.log(value);
    let twoArr = this.state.allMsg.categoryName.filter(item => parseInt(value) === item.bparentId)
    let newTwoArr = []
    twoArr.forEach(item => {
      newTwoArr.push({ name: item.bname, id: item.categoryTwo })
    })
    // console.log(newTwoArr);
    const id = 'id';
    const arr = newTwoArr.reduce((all, next) => all.some((atom) => atom[id] === next[id]) ? all : [...all, next], []);
    this.setState({
      gradeTwo: arr,
      aa: '545454'
    })
  }
  //  
  twoChange = value => {
    let threeArr = this.state.allMsg.categoryName.filter(item => parseInt(value) === item.cparentId)
    // console.log(threeArr);
    let newThreeArr = []
    threeArr.forEach(item => {
      newThreeArr.push({ name: item.cname, id: item.categoryThree })
    })
    // console.log(newThreeArr);
    const id = 'id';
    const arr = newThreeArr.reduce((all, next) => all.some((atom) => atom[id] === next[id]) ? all : [...all, next], []);
    // console.log(arr);
    this.setState({
      gradeThree: arr
    })
  }
  //
  getSup = () => {
    let supName = 'supName'
    console.log(this.state.allMsg.supName);
    if (this.state.allMsg.supName) {
      let data = this.state.allMsg.supName.reduce((all, next) => all.some((atom) => atom[supName] === next[supName]) ? all : [...all, next], []);
      console.log(data);
      let temp = []
      data.forEach(item => {
        temp.push(<Option key={item.supId} value={item.supId}>{item.supName}</Option>)
      })
      return temp
    }

  }
  render() {
    let dataSource = [
      {
        key: '0',
        classification1: <Form.Item
          name='categoryOne'
          rules={[
            {
              required: true,
              message: '1!',
            },
          ]}
        ><Select size="small" placeholder="1" onChange={this.oneChange}>
            {this.getOne()}
          </Select></Form.Item>,
        classification2: <Form.Item
          name='categoryTwo'
          rules={[
            {
              required: true,
              message: '2!',
            },
          ]}
        ><Select size="small" placeholder="2" onChange={this.twoChange} notFoundContent={<div></div>}>
            {this.getTwo()}
          </Select></Form.Item>,
        classification3: <Form.Item
          name='categoryThree'
          rules={[
            {
              required: true,
              message: '3!',
            },
          ]}
        ><Select size="small" placeholder="3" notFoundContent={<div></div>}>
            {this.getThree()}
          </Select></Form.Item>,
        num: <Form.Item
          name='count'
          rules={[
            {
              required: true,
              message: '!',
            },
          ]}
        >
          <Input size='small' placeholder="" />
        </Form.Item>
      },
    ];

    //
    const onFinish = values => {
      let that = this
      console.log('Received values of form: ', values);
      Axios.post('/logistics', values)
        .then(res => {
          if (res.status === 200) {
            that.props.history.push({
              pathname: "/index/List",
            })
            if (res.data.code === '0000') {
              message.success('');

            } else {
              message.error('');
            }
          }
          console.log(res);
        })
    };
    return (
      <div id='newLogistics' style={{ padding: '20px' }}>
        <div id='goBack' style={{
          paddingBottom: '15px',
          marginBottom: '10px',
          borderBottom: '1px solid #ccc',
        }}>
          <Button size="small" onClick={this.goBackPage}></Button>
        </div>

        <Form
          name="Logistics_search"
          onFinish={onFinish}
        >
          <Title level={4}></Title>
          <Row gutter={24}>
            <Col span={8} key='1'>
              <Form.Item
                name='userName'
                label=':'
                rules={[
                  {
                    required: true,
                    message: '!',
                  },
                ]}
              >
                <Input size='small' />
              </Form.Item>
              <Form.Item
                name='userPhoneNumber'
                label=':'
                rules={[
                  {
                    required: true,
                    message: '!',
                  },
                ]}
              >
                <Input size='small' />
              </Form.Item>
            </Col>
            <Col span={8} key='2'>
              <Form.Item
                name='consigneeName'
                label=':'
                rules={[
                  {
                    required: true,
                    message: '!',
                  },
                ]}
              >
                <Input size='small' />
              </Form.Item>
              <Form.Item
                name='consigneePhoneNumber'
                label=':'
                rules={[
                  {
                    required: true,
                    message: '!',
                  },
                ]}
              >
                <Input size='small' />
              </Form.Item>
            </Col>
            <Col span={8} key='3'>
              <Form.Item
                name='street'
                label=':'
                rules={[
                  {
                    required: true,
                    message: '!',
                  },
                ]}
              >
                <Input size='small' placeholder="" />

              </Form.Item>
              <Form.Item
                name='consigneeAddress'
                label=':'
                rules={[
                  {
                    required: true,
                    message: '!',
                  },
                ]}
              >
                <Input size='small' placeholder="" />
              </Form.Item>
            </Col>
          </Row>
          <Title level={4}></Title>
          <Row gutter={24}>
            <Col span={8} key='4'>
              <Form.Item
                name='agingCode'
                label=''
                rules={[
                  {
                    required: true,
                    message: '!',
                  },
                ]}
              >
                <Select size="small" placeholder="">
                  <Option value=""></Option>
                  <Option value=""></Option>
                </Select>
              </Form.Item>
            </Col>
          </Row>
          <Title level={4}></Title>
          <Row gutter={24}>
            <Col span={15} key='5'>

              <Table
                pagination={false}
                locale={{ emptyText: '' }}
                // components={components}
                rowClassName={() => 'editable-row'}
                bordered
                dataSource={dataSource}
                columns={this.columns}
                // pagination={paginationProps}
                size='small'
              />



            </Col>
            <Col offset='2' span={7} key='1'>
              {/* <Form.Item
                                name='count'
                                label=':'
                                rules={[
                                    {
                                        required: true,
                                        message: '!',
                                    },
                                ]}
                            >
                                <Input size='small'/>
                            </Form.Item> */}
              <Form.Item
                name='heavy'
                label=':'
                rules={[
                  {
                    required: true,
                    message: '!',
                  },
                ]}
              >
                <Input size='small' />
              </Form.Item>
              <Form.Item
                name='price'
                label=':'
                rules={[
                  {
                    required: true,
                    message: '!',
                  },
                ]}
              >
                <Input size='small' />
              </Form.Item>
            </Col>
          </Row>
          <Title level={4}></Title>
          <Row gutter={24}>
            <Col span={8} key='1'>
              <Form.Item
                name='supId'
                label=':'
                rules={[
                  {
                    required: true,
                    message: '!',
                  },
                ]}
              >
                <Select size="small" placeholder="">
                  {this.getSup()}
                </Select>
              </Form.Item>
            </Col>
            <Col offset='3' span={8} key='2'>
              <Form.Item
                name='supCount'
                label=':'
                rules={[
                  {
                    required: true,
                    message: '!',
                  },
                ]}
              >
                <Input size='small' />
              </Form.Item>

            </Col>
          </Row>
          <Row>
            <Col
              span={24}
              style={{
                textAlign: 'center',
              }}
            >
              <Button type="primary" htmlType="submit">
                
                            </Button>
            </Col>
          </Row>
        </Form>
      </div>
    )
  }
}

export default newLogistics