import React, { Component } from 'react'
import { Button, Divider, Table, Typography, Row, Col, Spin,message } from "antd";
import Axios from '../../util/Axios'

import '../../css/zj/OrderDetailsZ.css'

const { Title } = Typography;


class OrderDetails extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isloaging:true,
      orderDetailsMsg: '',//
      orderDetails: '',//
      columns: [
        {
          title: '1',
          dataIndex: 'classification1',
          align: 'center'
        },
        {
          title: '2',
          dataIndex: 'classification2',
          align: 'center'
        },
        {
          title: '',
          dataIndex: 'goodsNum',
          align: 'center'
        }
      ]
    }
  }
  // 
  componentDidMount() {
    // console.log(this.props.location.state);
    console.log(this.props.location.state.signNumber);

    // console.log(this.state.orderDetailsMsg)
    // console.log(this.state.orderDetailsMsg.orderNumber)
    Axios.post('/Order/findOderByLg', {
      logisticsId: this.props.location.state.signNumber
    })
      .then(res => {
        console.log(res);
        console.log(res.data);
        if(res.data.msg === ''){
          let myData = [];
          res.data.data.orderOneToThreeList.forEach((val, index) => {
            myData.push({
              key: index,
              classification1: val.twoName,
              classification2: val.threeName,
              goodsNum: val.count
            })
          })
          this.setState({
            orderDetailsMsg: res.data.data,
            orderDetails: myData,
            isloaging:false
          })
        }else{
          message.error(res.data.msg);
        }
      
      })
  }
  //
  goBackPage = () => {
    window.history.back(-1)
  };


  render() {
    //
    let paginationProps = '';
    if (this.state.orderDetails.length >= 10) {
      paginationProps = true
    } else {
      paginationProps = false

    }
    return (
      <div id='details'>
        <Spin spinning={this.state.isloaging}>
          <div className='detailsTitle'>
            <Button size="small" onClick={this.goBackPage}></Button>
            <p>{this.state.orderDetailsMsg.checkStatus}</p>
          </div>
          <Divider />
          <div id='detailsMsg'>
            <Row className='rowBox' gutter={24}>
              <Col key='1'>
                <p>{this.state.orderDetailsMsg.id}</p>
              </Col>
              <Col key='2'>
                <p>{this.state.orderDetailsMsg.userName}</p>
              </Col>
              <Col key='3'>
                <p>{this.state.orderDetailsMsg.consigneeName}</p>
              </Col>
              <Col key='4'>
                <p>{this.state.orderDetailsMsg.consigneePhoneNumber}</p>
              </Col>
              <Col key='5'>
                <p>{this.state.orderDetailsMsg.consigneeAddress}</p>
              </Col>
            </Row>
            <Row className='rowBox' gutter={24}>
              <Col key='1'>
                <p>{this.state.orderDetailsMsg.staffName}</p>
              </Col>
              <Col key='2'>
                <p>{this.state.orderDetailsMsg.staffPhoneNumber}</p>
              </Col>
              <Col key='3'>
                <p>{this.state.orderDetailsMsg.heavy}</p>
              </Col>
              <Col key='4'>
                <p>{this.state.orderDetailsMsg.street}</p>
              </Col>
              <Col key='5'>
                <p>{this.state.orderDetailsMsg.siteName}</p>
              </Col>
            </Row>
          </div>
          <Divider />

          <div id="shoppingVoucher">
            <Title level={4}></Title>
            <div className="VoucherMsg">
              <div>
                <p>:</p>
                <p>{this.state.orderDetailsMsg.cashNumber}</p>
              </div>
              <div>
                <p>:</p>
                <p>{this.state.orderDetailsMsg.serialNumber}</p>
              </div>
              <div>
                <p>:</p>
                <p>{this.state.orderDetailsMsg.bronTime}</p>
              </div>
            </div>

          </div>
          <Divider />
          <div id='orderDetails'>
            <Title level={4}></Title>
            <div>
              <Table size='small' columns={this.state.columns} dataSource={this.state.orderDetails} bordered style={{ width: 300 }}
                pagination={paginationProps} />
              <p>{this.state.orderDetailsMsg.supName}{this.state.orderDetailsMsg.supCount}</p>
              <p>{this.state.orderDetailsMsg.heavy} KG</p>
              <p>{this.state.orderDetailsMsg.price} </p>
              <p>{this.state.orderDetailsMsg.supName}{this.state.orderDetailsMsg.supCount} </p>
            </div>
            <div>
              <p></p>
              <p>{this.state.orderDetailsMsg.deliveryTime}</p>
            </div>
            <div>
              <p>{this.state.orderDetailsMsg.pickTime}</p>
              <p>{this.state.orderDetailsMsg.finishTime}</p>
            </div>
          </div>
        </Spin>
      </div>
    )
  }
}

export default OrderDetails